.Contact {
 
    background-color: #252525;
 height: 400px;
    margin-top:0;
    }
    
    .Contact h1 {
      position: relative;
      top: 60px;
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    font-size: 43px;
    font-family:  "brandon-grotesque", Helvetica, Arial, sans-serif;
    }
    
    .Contact p {
      position: relative;
      top: 70px;
    color:#fff;
    text-align: center;
    font-family:  "brandon-grotesque", Helvetica, Arial, sans-serif;
    font-size: 25px;
    padding-bottom: 90px;
    }
      
.icons {
 
 display: flex;
  justify-content:center;
cursor: pointer;
  flex-wrap: wrap;
  gap: 40px;

  
}
.icons a {
  display: block;
  position: relative;
  overflow: hidden;
 width: 90px;
  text-align: center;
 padding: 12px 0;
  transition: all 0.3s ease;
  color: #fff;
  font-size: 10px;
  cursor: pointer;
}



.icons a:hover {
  background-color: #fff;
color:#d24d57;

}

.active {
  background-color: #d24d57;
  color:#fff;
}



@media only screen and (max-width: 430px) {
 .Contact {
   height: 550px;
 }
    
    }